import {
  Box,
  Button,
  Container,
  Flex,
  Grid,
  GridItem,
  Image,
  Text,
} from "@chakra-ui/react";
import React from "react";
import avatar from "../assets/avatar.png";
import { chakra, shouldForwardProp } from "@chakra-ui/react";
import { motion, isValidMotionProp } from "framer-motion";
import { Cursor, useTypewriter } from "react-simple-typewriter";

const ChakraBox = chakra(motion.div, {
  /**
   * Allow motion props and non-Chakra props to be forwarded.
   */
  shouldForwardProp: (prop) =>
    isValidMotionProp(prop) || shouldForwardProp(prop),
});

const Hero = () => {
  const [text] = useTypewriter({
    words: ["Full Stack Developer."],
    loop: 0,
  });

  return (
    <Container maxW="4xl" minH="89vh">
      <Flex minH="89vh" justifyContent="center" alignItems="center">
        <ChakraBox
          initial={{ y: -15, opacity: 0 }}
          animate={{ y: 0, opacity: 1 }}
          transition={{
            duration: 1.2,
            delay: 0.5,
          }}
          w="100%"
        >
          <Grid
            templateColumns={{ base: "repeat(2, 1fr)", md: "repeat(4, 1fr)" }}
            templateAreas={{
              base: `"avatar avatar" "name name"`,
              md: `"name name name avatar"`,
            }}
            gap={{ base: 20, md: 4 }}
            marginBottom="50px"
          >
            <GridItem
              // colSpan={{ base: "2", md: "3" }}
              marginX={{ base: "0", md: "0" }}
              area={"name"}
            >
              <Text fontSize={{ base: "26px", sm: "3xl" }} fontWeight="600">
                Hello, my name is
              </Text>
              <Text fontSize={{ base: "40px", sm: "70px" }} fontWeight="600">
                Mohd Hasan
              </Text>
              <Text fontSize={{ base: "26px", sm: "3xl" }} fontWeight="600">
                I'm a <span style={{ color: "#007FFF" }}>{text}</span>
                <Cursor cursorColor="#007FFF" />
              </Text>
              <Button
                size="lg"
                marginTop="20px"
                bg="textPrimary"
                _hover={{
                  color: "textPrimary",
                  bg: "bgPrimary",
                  border: "1px solid #007FFF",
                }}
              >
                Resume
              </Button>
            </GridItem>
            <GridItem area={"avatar"}>
              <Box h="270px" w="270px" marginX="auto" position="relative">
                <Box
                  h="270px"
                  w="270px"
                  bg="textPrimary"
                  position="absolute"
                  inset={0}
                  filter="blur(10px)"
                  zIndex="-1"
                  rounded="full"
                ></Box>
                <Image src={avatar} rounded="full"></Image>
              </Box>
            </GridItem>
          </Grid>
        </ChakraBox>
      </Flex>
    </Container>
  );
};

export default Hero;
